import React,{Component} from 'react';
import {  } from 'react-router-dom'
import SummonerInfo from './components/SummonerInfo'

class SummonerPage extends Component {
  state = { 
    accountInfo: "",
  }

  componentDidMount(){
    const xhl = new XMLHttpRequest();
    // xhl.open("GET", "https://bypasscors.herokuapp.com/api/?url=https://eun1.api.riotgames.com/lol/summoner/v4/summoners/by-name/g2%20kylo?api_key=RGAPI-6bf98ac9-17c5-4281-9d4a-22d8650bf794");
    xhl.open("POST", "https://bypasscors.herokuapp.com/api/?url=https://leagueoflegendsapp.netlify.com/.netlify/functions/getriotapi");
    xhl.setRequestHeader("Content-Type", "application/json")
    xhl.send(JSON.stringify({
      "data": {
        "type" : "summoner",
        "nickname":"Ishvalczyk",
        "region":"eun1"
      },
      "headers" : {
        "Content-Type": "application/json"
      }
    }));

    console.log(JSON.parse(xhl.response));
    
    if(xhl.status === 200){
      this.setState({
        accountInfo: JSON.parse(xhl.response)
      })
    }
  }

  render() {
    console.log(this.state.accountInfo);
    return (
    <div className="summonerpage">
      <div className="container-fluid">
        <div className="container">
          <header>
            <SummonerInfo accountInfo={this.state.accountInfo}/>
          </header>
        </div>
      </div>
    </div>
    );
  }
}

export default SummonerPage;